* {
  color: white;
}

html, body {height:100%;
margin:0;}




body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

.bg {

  background-image: url("/src/assets/newbg.jpg");


  height: 50%;


  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.screenshot {
  width: auto;
  max-height: 80%;
  border: black;
  border-width: 3pt;
  border-style: solid;
}

.linkImg {
  max-width: 10%;
  height: auto;
  padding: 5px;
}

.homeText {
  font-family: 'Open Sans', sans-serif;
  font-size: 50pt;
  font-weight: bolder;
}

.buffer {
  padding: 10px;
}

h2 {
  color: black
}

p {
  color: black;
}

.buttonImg {
  width: 5%;
  height: 5%;
}

.vert {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.aboutText{
  color:white;
  text-align: center;
  font-weight: bold;
  font-family: 'Open Sans', sans-serif;
  list-style-type: none;
}

#resume{
  max-height: 80%;
  max-width: auto;

}

.navbar-right{
  align-self: right;
}

.nav-link {
  color:white;
}

.navbar-brand {
  color:white;
}

.wrapper {
  min-height: 100%;
  margin-bottom: -50px;
}

.footer,
.push {
  height: 50px;
}

.homeBuffer{
  height:50%;


}